/* eslint-disable no-unused-vars */
<template>
  <div class="chart">
    <canvas ref="canvas"></canvas>
  </div>
</template>

<script>
import Chart from 'chart.js'

const red = 'rgba(255,100,100,0.8)';
const green = 'rgba(19, 206, 102,0.8)';

export default {
  data() {
    return {
      chartsInstance: null,
      config: {
        type: 'bubble',
        data: {
          datasets: [{
            data: [],
          }]
        },
        options: {
          // aspectRatio: 4,
          responsive: true,
          legend: false,
          tooltips: {
            // backgroundColor: 'transparent',
            // titleFontColor: '#000',
            titleMarginBottom: -2,
            titleAlign: 'center',
            callbacks: {
              title(tooltipItem, data) {
                const stock = data.datasets[tooltipItem[0].datasetIndex].data[tooltipItem[0].index].stock

                return `${stock.name} ${stock.change}%`
              },
              label() {
                return ''
              }
            }
          },
          scales: {
            xAxes: [{
              gridLines: {
                display: false
              },
              ticks: {
                display: false
              },         
            }],
            yAxes: [{
              gridLines: {
                display: false
              },
              // ticks: {
              //   min: -20,
              //   max: 20,
              //   // stepSize: 0.1,
              // }
            }]
          },
          elements: {
            point: {
              borderColor: 'rgba(0,0,0,0)',
              hoverRadius: () => {
                return 30;
              }
            }
          },
        },
      }
    }
  },
  mounted() {
    var ctx = this.$refs.canvas.getContext('2d')
    
    Chart.defaults.global.defaultFontFamily = `"Microsoft Yahei", monospace`
    Chart.defaults.global.defaultFontSize = `12`
    
    this.chartsInstance = new Chart(ctx, this.config)
  },
  methods: {
    randomScalingFactor() {
      return (Math.random() > 0.5 ? 1.0 : -1.0) * Math.round(Math.random() * 20);
    },
    createDatasets({ stocks, isRandom = false }) {
      const datasets = [{
        backgroundColor: red,
        data: []
      }, {
        backgroundColor: green,
        data: []
      }];

      let index = 1
      
      for(let stock of stocks) {
        const point = {
          x: isRandom ? this.randomScalingFactor() : index++,
          y: stock.change,
          r: (stock.weight + 2)*3,
          stock,
        }
        
        if (stock.change >= 0) {
          datasets[0].data.push(point)
        } else {
          datasets[1].data.push(point)
        }
      }

      return datasets
    },
    destory() {
      if (this.chartsInstance) { this.chartsInstance.destroy() }
    },
    clear() {
      if (this.chartsInstance) { this.update({ stocks: [] }) }
    },
    update({ stocks, isRandom = false }) {
      const datasets = this.createDatasets({ stocks, isRandom })

      this.config.data.datasets = datasets
      if(this.chartsInstance) { this.chartsInstance.update(this.config) }
    }
  }
}
</script>

<style scoped>
  .chart {
    width: 100%;
    margin: auto;
  }

  canvas{
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
  }
</style>
